import React, { useEffect } from 'react'
import styled from 'styled-components'
import { Container } from '../globalStyle'
import BackgroundImg from '../img/main-banner.png'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

const Section = styled.section`
    height: 1600px;
    position: relative;
    overflow: hidden;
    z-index: 2;
    @media(max-width: 1300px) {
        height: 1100px;
    }
    @media(max-width: 992px) {
        height: 850px;
    }
    @media(min-width: 2500px) {
        height: 2200px;
    }

    h1 span {
        -webkit-text-stroke: 1px #fff;
        font-size: 80px;
        line-height: 96px;
        font-weight: 400;
        font-family: sans-serif;
        color: transparent;
        @media(min-width: 1920px) {
            font-size: 120px;
            line-height: 100%;
        }
    }
    .promo-img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transform-origin: center;
    }
    .char {
        transition: color .5s cubic-bezier(0.215, 0.610, 0.355, 1);
        position: relative;
        
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 10px;
            height: 10px
        }
    }
    .light {
        color: #fff;
    }
    .container {
        height: 100%;
        position: relative;
        z-index: 2;
    }
`

const SectionWrapper = styled.div`
    height: 100%;
`

const PromoHeading = styled.div`
    max-width: 640px;
    @media(min-width: 1920px) {
        max-width: 900px;
    }
`

const PromoDescription = styled.div`
    p {
        max-width: 387px;
        font-weight: 400px;
        font-size: clamp(22px, 1.66667vw, 28px);
        color: #fff;
        line-height: 1.6em;
        @media(min-width: 1920px) {
            max-width: 487px;
        }
    }
`

const PromoContent = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 64px 0;
    @media(min-width: 1920px) {
        padding: 86px 0;
    }
`

const PromoSection = () => {

    useEffect(() => {

        // LETTERS CHANGE COLOR
        const heading = document.querySelector('.promo-heading span')
        const charsArray = heading.textContent.trim().split('')
        heading.textContent = ''
    
        let firstRow = 15
        let secondRow = 16
        let thirdRow = 12
        let paddingValue = 6
    
        let totalPaddingFirstRow = firstRow * paddingValue;
        let totalPaddingSecondRow = secondRow * paddingValue;
        let totalPaddingThirdRow = thirdRow * paddingValue;
    
        for(let i = 1; i <= charsArray.length; i++) {
            heading.innerHTML += `<span class="char char-${i}">${charsArray[i - 1]}</span>`
        }
    
        for(let i = 1; i <= firstRow; i++) {
          totalPaddingFirstRow -= paddingValue
          document.querySelector(`.char-${i}`).style.paddingTop = totalPaddingFirstRow +'px'
        }
        for(let i = secondRow; i <= 30; i++) {
          totalPaddingSecondRow -= paddingValue
          document.querySelector(`.char-${i}`).style.paddingTop = totalPaddingSecondRow +'px'
        }
        for(let i = 31; i <= 36; i++) {
          totalPaddingThirdRow -= paddingValue
          document.querySelector(`.char-${i}`).style.paddingTop = totalPaddingThirdRow +'px'
        }
    
        gsap.utils.toArray('.char').forEach(char => {
    
          ScrollTrigger.create({
            trigger: char,
            start: '-=80',
            end: '+=500',
            toggleClass: 'light'
          })
    
        })
        // ===

        // SCALE IMAGE
        let t1 = gsap.timeline({
            scrollTrigger: {
                trigger: '.promo-img',
                start: '-=10',
                end: 'bottom bottom',
                scrub: 0.8,
            }
        })

        t1.to('.promo-img', {scale: 1.2})
        // ===
    
    }, [])
    
    return (
        <Section className="promo-section panel">
            <img className="promo-img" src={BackgroundImg} alt="Swim System" />
            <SectionWrapper>
                <Container className="container">
                    <PromoContent>
                        <PromoHeading className="promo-headcon">
                            <h1 className="promo-heading">
                                <span>
                                    Swim System це сила та енергія рухів
                                </span>
                            </h1>
                        </PromoHeading>
                        <PromoDescription className="promo-description">
                            <p>
                            Swim System - це багатогранна складова, яка складається
    з цікавого і послідовного тренувального процесу, помножена на здорову атмосферу і захопленість людей. Це допомагає досягати цілей і робити себе краще.
                            </p>
                        </PromoDescription>
                    </PromoContent>
                </Container>
            </SectionWrapper>
        </Section>
    )
}

export default PromoSection